export async function requestSubtitle(videoId, caption) {
  try {
    const response = await fetch('/api/request-subtitle', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        videoId,
        caption,
      })
    });

    if (!response.ok) {
      throw new Error('  : ${response.status}');
    }

    return await response.blob();
  } catch (error) {
    console.error('   :', error);
    throw error;
  }
}